.apps_tab .nav-link.active,
.apps_tab .nav-link:hover{
    background-color: var(--bs-primary);
    color: var(--bs-white);
}

.apps_tab .nav-link{
    background-color: var(--bs-body-bg);
    color: var(--bs-body-color);
}

.apps_tab .nav-link i{
    color: var(--bs-gray-500);
}

.apps_tab .nav-link:hover i,
.apps_tab .nav-link.active i{
    color: var(--bs-white);
}